@import url(../../bower_components/normalize-css/normalize.css);
/* VARIABLES */
/* ----------------------------------------- */
/* -- Colors -- */
.primary {
  color: #e74c3c;
}

.primary-bg {
  background-color: #e74c3c;
}

.secondary-sky-blue {
  color: #2d82d8;
}

.secondary-dark-blue-bg {
  background-color: #34495e;
}

/* BASIC SETUP */
/* ----------------------------------------- */
/* ----------------------------------------- */
/* GLOBAL SETTINGS */
/* ----------------------------------------- */
/* -- Global Reset -- */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #fff;
  color: #333;
  font-family: 'Lato', 'Arial', sans-serif;
  font-weight: 300;
  /* Default font-weight */
  font-size: 20px;
  /* Base font-size */
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
}

body a:focus {
  outline: none;
}

body h1 {
  color: #34495e;
  font-weight: 300;
  font-size: 48px;
}

body h3 {
  font-size: 95%;
  color: #333;
  margin: 0 0 15px 0;
  font-weight: 500;
}

body .img-circle {
  border-radius: 50%;
}

.clearfix {
  zoom: 1;
}

.clearfix:after {
  content: ".";
  clear: both;
  display: block;
  height: 0;
  visibility: hidden;
}

/* REUSEABLE COMPONENTS */
/* ----------------------------------------- */
/* SECTIONS */
/* ----------------------------------------- */
.site-header .navbar-inverse {
  background-color: #34495e;
  border: none;
}

.site-header .navbar {
  color: #fff;
  vertical-align: middle;
  padding: 20px 0;
}

.site-header .navbar-brand {
  padding: 0;
}

.site-header .navbar-nav {
  font-weight: 700;
  text-transform: uppercase;
  font-size: 14px;
}

.site-header .navbar-nav li a {
  color: #fff;
  transition: all 0.3s ease-in;
}

.site-header .navbar-nav li a:hover {
  color: #e74c3c;
}

.site-header .navbar-nav li.active a,
.site-header .navbar-nav li.active a:visited,
.site-header .navbar-nav li.active a:hover,
.site-header .navbar-nav li.active a:active,
.site-header .navbar-nav li.active a:focus {
  background-color: transparent;
  color: #e74c3c;
}

.section-slider {
  background-color: #f4f4f4;
  padding: 0 0 40px 0;
}

.section-slider #owl-demo .item img {
  display: block;
  width: 100%;
  height: auto;
}

.section-slider .text-block {
  margin: 20px 30px 40px;
}

.section-slider .text-block p {
  margin-bottom: 40px;
}

.section-slider .text-block a {
  padding: 10px 25px;
  color: #fff;
  border-radius: 6px;
  font-weight: bold;
  transition: all 0.3s ease-in;
}

.section-slider .text-block a:hover {
  text-decoration: none;
}

.section-slider .text-block a:last-child {
  margin-left: 5px;
}

.section-slider .text-block a.cta1:hover {
  background-color: #ff695a;
}

.section-slider .text-block a.cta2:hover {
  background-color: #3f9cfa;
}

.section-slider .item {
  margin-top: 150px;
}

.section-slider .item h1 {
  color: #34495e;
  font-weight: 300;
  font-size: 48px;
}

.section-slider .item .btn {
  font-size: 16px;
  border: none;
}

.section-slider .item .btn-primary {
  color: #fff;
  background-color: #e74c3c;
}

.section-slider .item .btn-primary:hover {
  background-color: #d62c1a;
}

.section-slider .item .btn-secondary {
  background-color: #34495e;
  color: #fff;
}

.section-slider .item .btn-secondary:hover {
  background-color: #46627f;
}

.services {
  padding: 56px 0;
}

.services .services-header {
  margin-bottom: 40px;
}

.services .box {
  margin-bottom: 30px;
}

.services .box p {
  padding: 0 0px;
  font-size: 80%;
}

.services .box .fa {
  font-size: 32px;
  color: #34495e;
}

.portfolio {
  padding: 56px 0;
  background-image: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url("/public/images/progect/portfolio-bg.jpg");
  background-attachment: fixed;
  background-size: 100% 100%;
}

.portfolio h1, .portfolio .lead {
  color: #fff;
}

.portfolio #owl-demo .item2 img {
  display: block;
  width: 100%;
  height: auto;
  color: white;
}

.portfolio .owl-controls {
  margin-top: 20px;
}

.portfolio .overlay-content h3, .portfolio .overlay-content p, .portfolio .overlay-content a {
  padding: 0;
  margin: 0;
  color: #333;
  margin-top: 10px;
}

.portfolio .overlay-content p, .portfolio .overlay-content a {
  font-size: 85%;
  font-weight: bold;
  color: #fff;
  margin: 0;
}

.portfolio .overlay-content p a, .portfolio .overlay-content a a {
  color: #2d82d8;
}

.portfolio .overlay-content .owl-content {
  color: white;
  position: absolute;
  top: 0;
  left: 30px;
}

.team {
  padding: 56px 0;
}

.team h1, .team .lead {
  color: #34495e;
}

.team #owl-demo .item3 img {
  display: block;
  width: 100%;
  height: auto;
  color: #333;
}

.team .owl-controls {
  margin-top: 20px;
}

.team .item3 img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: 1px solid #000;
}

.team .overlay-content2 h3, .team .overlay-content2 p, .team .overlay-content2 a {
  padding: 0;
  margin: 0;
  color: #333;
  margin-top: 10px;
}

.team .overlay-content2 h3.name {
  font-weight: bold;
  color: #e74c3c;
}

.team .overlay-content2 .position {
  font-weight: normal;
  color: #2d82d8;
}

.team .overlay-content2 .description {
  font-weight: normal;
  font-size: 65%;
  padding: 0 15px;
  text-align: center;
}

.team .overlay-content2 li {
  display: inline-block;
}

.team .overlay-content2 li .fa {
  width: 38px;
  height: 38px;
  line-height: 38px;
  border-radius: 50%;
  color: #fff;
  margin-top: 15px;
  font-weight: 300;
}

.team .overlay-content2 li .fa-facebook {
  background-color: #3b5998;
}

.team .overlay-content2 li .fa-twitter {
  background-color: #00aced;
}

.team .overlay-content2 li .fa-linkedin {
  background-color: #007bb6;
}

.team .overlay-content2 li .fa-facebook:hover {
  background-color: #4c70ba;
}

.team .overlay-content2 li .fa-twitter:hover {
  background-color: #21c2ff;
}

.team .overlay-content2 li .fa-linkedin:hover {
  background-color: #0091de;
}

.team .overlay-content2 p, .team .overlay-content2 a {
  font-size: 85%;
  font-weight: bold;
  color: #333;
  margin: 0;
}

.team .overlay-content2 p a, .team .overlay-content2 a a {
  color: #2d82d8;
}

.team .overlay-content2 .owl-content {
  color: white;
  position: absolute;
  top: 0;
  left: 30px;
}

.testimonials {
  padding: 56px 0;
  background-image: url("/public/images/progect/testimonial-bg.png");
  background-attachment: fixed;
  background-size: 100% 100%;
}

.testimonials h1, .testimonials .lead {
  color: #fff;
}

.testimonials .overlay-content4 {
  color: #fff;
}

.testimonials .overlay-content4 p {
  margin: 0;
  padding: 0;
}

.testimonials .overlay-content4 p.name {
  font-weight: 500;
  margin-top: 15px;
  font-size: 150%;
}

.testimonials .overlay-content4 p.position {
  color: #2d82d8;
  font-weight: bold;
  margin-bottom: 15px;
}

.testimonials .overlay-content4 li {
  list-style: none;
  margin-bottom: 20px;
}

.testimonials .overlay-content4 li img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: 2px solid #fff;
}

.testimonials .overlay-content4 .owl-content {
  color: white;
  position: absolute;
  top: 0;
  left: 30px;
}

.testimonials .overlay-content4 .owl-page span {
  background-color: #2d82d8;
}

.testimonials .overlay-content4 blockquote {
  display: inline-block;
  color: #fff;
  border: none;
  width: 70%;
  margin: 0 auto;
  padding: 0;
  line-height: 1.5em;
  font-style: italic;
  position: relative;
}

.testimonials .overlay-content4 blockquote:before {
  position: absolute;
  content: "\f10d";
  top: -10px;
  left: -20px;
  font-family: fontawesome;
}

.testimonials .overlay-content4 blockquote:after {
  position: absolute;
  content: "\f10e";
  bottom: -10px;
  right: -20px;
  font-family: fontawesome;
}

.blog {
  padding: 56px 0;
}

.blog .container h3.post-name {
  color: #2d82d8;
  font-size: 24px;
  font-weight: 700;
  margin-top: 15px;
  margin-bottom: 5px;
  transition: all 0.3s ease-in;
}

.blog .container h3.post-name a {
  text-decoration: none;
}

.blog .container h3.post-name a:hover {
  color: #589ce0;
}

.blog .container p {
  padding: 0;
  margin: 0 0 10px 0;
  font-size: 85%;
}

.blog .container img {
  margin-right: 20px;
}

.blog .container img:last-child {
  margin-right: 0px;
}

.blog .container .date {
  color: #333;
}

.contact {
  padding: 56px 0;
  background-image: url("/public/images/contact/contact-bg.jpg");
  background-attachment: fixed;
  background-size: 100% 100%;
  color: #fff;
}

.contact h1, .contact .lead {
  color: #fff;
}

.contact form {
  width: 100%;
  text-align: center;
}

.contact form input:not([type=submit]), .contact form textarea {
  width: 100%;
  margin-bottom: 20px;
  color: #777;
  padding: 10px;
  line-height: 40px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.2) inset;
  border: none;
}

.contact form input:not([id=message]) {
  height: 50px;
}

.contact form textarea {
  padding-top: 5px;
}

.contact form #submit {
  padding: 10px 30px;
  background-color: #34495e;
  color: #fff;
  border: solid 1px #fff;
  font-weight: 700;
  width: 50%;
  border-radius: 10px;
  letter-spacing: .5em;
  text-transform: uppercase;
  transition: all 0.3s ease-in;
}

.contact form #submit:hover {
  background-color: #e95a4b;
}

footer {
  padding: 56px 0;
}

footer .content p {
  padding: 0;
  width: 85%;
  font-size: 90%;
  margin: 0;
}

footer .content li {
  padding: 0;
  margin: 0;
  list-style: none;
  font-size: 80%;
  font-weight: bold;
}

footer .content i {
  color: #2d82d8;
  text-align: center;
  margin: 5px 20px 5px 0;
  font-size: 24px;
}

footer #get-email {
  padding: 5px 10px;
  width: 90%;
  margin-bottom: 15px;
  height: 40px;
  line-height: 40px;
}

footer #submit-button {
  background-color: #34495e;
  border: none;
  color: #fff;
  padding: 6px 20px;
  font-weight: 700;
  border-radius: 5px;
}

footer .footer-social-links {
  width: 100%;
  float: right;
}

footer .footer-social-links li i {
  float: left;
  display: inline-block;
  padding: 5px;
  margin-right: 7px;
  border: 1px solid #999;
  border-radius: 3px;
}

footer .footer-social-links .fa-facebook {
  color: #3b5998;
}

footer .footer-social-links .fa-google-plus {
  color: #dd4b39;
}

footer .footer-social-links .fa-twitter {
  color: #55acee;
}

footer .footer-social-links .fa-dribbble {
  color: #ea4c89;
}

footer .footer-social-links .fa-youtube {
  color: #bb0000;
}

.copyright {
  text-align: center;
  background-color: #333;
  padding: 20px 0;
  color: #fff;
  font-size: 80%;
  font-weight: 500;
}

.copyright span {
  color: #e74c3c;
}

/* SECTIONS */
/* ----------------------------------------- */
.slicknav_menu {
  display: none;
}

@media screen and (max-width: 992px) {
  #menu {
    display: none;
  }
  .slicknav_menu {
    display: inline-block;
    position: fixed;
    z-index: 1000;
    background: transparent;
    right: 0;
  }
}
